
.container {
    &.about-page {
      .text-zone {
        position: absolute;
        left: 13%;
        top: 40%;
        transform: translateY(-50%);
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
  
        h1 {
          font-size: 53px;
          font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
          color: #ffd700;
          font-weight: 400;
          margin-top: 0;
          margin-bottom: 40px;
          left: 10px;
          margin-left: 30px;
  
          &::before {
            content: '<h1>';
            font-style: italic;
            font-size: 18px;
            position: absolute;
            margin-top: -10px;
            opacity: 0.5;
            line-height: 18px;
            left: -1px;
          }
  
          &::after {
            content: '<h1/>';
            font-style: italic;
            font-size: 18px;
            line-height: 18px;
            position: absolute;
            top: 70px;
            right: 630px;
            opacity: 0.5;
          }
        }
  
        p {
          font-style: 13px;
          color: #fff;
          margin-left: 20px;
          font-weight: 300;
          font-size: 14px;
          min-width: fit-content;
          animation: pulse 1s;
  
          &:nth-of-type(1) {
            animation-delay: 1.1s;
          }
          &:nth-of-type(2) {
            animation-delay: 2.2s;
          }
          &:nth-of-type(3) {
            animation-delay: 3.3s;
          }
        }
      }
  
      .text-animate-hover {
        &:hover {
          color: #fff;
        }
      }
    }
  }
  
  .stage-cube-cont {
    width: 50%;
    height: 100%;
    top: 0;
    padding-top: 18%;
    margin-left: 0;
    position: absolute;
    right: 0;
    overflow: hidden;
  }
  
  .cube-spinner {
    animation: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(60% - 100px);
  
    div {
      position: absolute;
      width: 200px;
      height: 200px;
      border: 1px solid #ccc;
      background: rgba(255, 255, 225, 0.4);
      font-size: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 0 20px 0px lightyellow;
    }
  
    .face1 {
      transform: rotateX(-90deg) translateZ(100px);
    }
    .face2 {
      transform: rotateY(90deg) translateZ(100px);
    }
    .face3 {
      transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
    }
    .face4 {
      transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
    }
    .face5 {
        transform: rotateY(0deg) rotateZ(90deg) translateZ(100px);
        z-index: 1;
    }
      
  
    .face6 {
      transform: rotateY(-90deg) translateZ(100px) rotateZ(90deg);
    }
  }
  
  @keyframes spincube {
    from, to {
      transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16% {
      transform: rotateX(-90deg);
    }
    33% {
      transform: rotateY(-90deg) rotateZ(90deg);
    }
    50% {
      transform: rotateY(-180deg) rotateZ(90deg);
    }
    66% {
      transform: rotateY(-270deg) rotateZ(90deg);
    }
    83% {
      transform: rotateY(90deg) rotateZ(90deg);
    }
  }
  .eating-animation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  position: relative;

  .mouth {
    width: 100px;
    height: 100px;
    background-color: #ff0000; // Color of the mouth
    border-radius: 50%;
    animation: mouth-animation 2s ease infinite;
    position: relative;
  }

  .food {
    width: 20px;
    height: 20px;
    background-color: #00ff00; // Color of the food
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: food-animation 2s ease infinite;
  }
}

@keyframes mouth-animation {
  0%, 100% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(0.2);
  }
}

@keyframes food-animation {
  0%, 100% {
    transform: translate(-50%, -50%);
  }
  50% {
    transform: translate(-50%, 50%);
  }
}
